html { box-sizing: border-box;}

*, *:before, *:after {box-sizing: inherit}

.visuallyhidden:not(:focus):not(:active) { position: absolute; width: 1px; height: 1px; margin: -1px;border: 0;padding: 0;white-space: nowrap;-webkit-clip-path: inset(100%);clip-path: inset(100%);clip: rect(00 0 0);overflow: hidden;}

/*
font-family: 'Kanit', sans-serif;
font-family: 'Roboto Mono', monospace; */

body {
  font-family: 'Kanit', sans-serif;
  font-weight: 300;
  background-image: url(/retro-api-docs/static/media/background.d6f674bb.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

img {
  width: 90%;
  max-width: 500px;
  margin: 60px auto 20px;
  display: block;
}

h2,
h3,
caption {
  color: #e654f6;
  font-weight: 300;
  text-transform: uppercase;
}

h3 {
  font-size: 23px;
}


label,
p {
  font-size: 17px;
  line-height: 1.3;
}

th {
  font-size: 20px;
  font-weight: 300;
}

caption {
  font-size: 20px;
  text-align: left;
}

.route {
  font-family: 'Roboto Mono', monospace;
  font-weight: 700;
}

.required {
  font-weight: 700;
}

.wrapper {
  max-width: 1000px;
  width: 90%;
  margin: 0 auto;
}

.desc {
  color: white;
  text-align: center;
  text-shadow: 1px 1px rgba(47, 138, 243, 0.8);
  margin: 20px 0;
}

.register-login,
.docs {
  border-radius: 16px;
  box-shadow: 0 0 9px 5px rgba(47, 138, 243, 0.5);
  border: solid 3px #80bafc;
  background-color: rgba(1, 1, 16, 0.9);
  color: white;
  margin: 0 auto;
  text-align: center;
  padding: 20px 20px 30px;
  margin-bottom: 60px;
}

.register-login {
  max-width: 800px;
}

.docs {
  max-width: 1000px;
  letter-spacing: 1.25px;
  padding: 20px 60px 30px;
  margin-top: 50px;
}

.form-field {
  display: flex;
  flex-direction: column;
  width: 85%;
  max-width: 450px;
  margin: 15px auto;
  text-align: left;

}

input {
  box-shadow: 0 0 9px 5px rgba(237, 102, 250, 0.5);
  border: solid 3px #f292fc;
  background-color: rgba(62, 63, 181, 0.39);
  padding: 8px;
  margin-top: 10px;
  color: white;
  font-family: 'Kanit', sans-serif;
  letter-spacing: 1.3px;
  font-weight: 300;
}

button,
input[type="submit"] {
  border-radius: 8.8px;
  box-shadow: 0 0 7px 4px #5f2ff3;
  border: solid 2.4px #e654f6;
  background-color: #5f2ff3;
  color: white;
  letter-spacing: 1.4px;
  padding: 10px 25px 8px;
  margin: 10px 12px;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 16px;
}

button {
  background: none;
}

button:hover,
button:focus-within,
input[type="submit"]:hover,
input[type="submit"]:focus-within {
  background-color: #e654f6;
}

.highlight {
  font-weight: 500;
  font-weight: 700;
  color: #6daffa;
}

.route-box {
  text-align: left;
  margin: 50px 0 100px;
}

.route-box.api-general {
  margin: 50px 0;
}

.route-box.api-general:nth-of-type(2){
  margin-bottom: 100px;
}

.route {
  box-shadow: 0 0 9px 5px rgba(230, 84, 246, 0.5);
  border: solid 3px #f292fc;
  background-color: rgba(62, 63, 181, 0.39);
  padding: 12px 15px;
}

.route-box.api-general h3 {
  color: #80cefc;
}

.route-box.api-general .route {
  box-shadow: 0 0 9px 5px rgba(84, 195, 246, 0.5);
  border: solid 3px #92d2fc;
  word-break: break-all;
}


table {
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0px 1px 0px rgba(47, 138, 243, 0.5);
  border-bottom: solid 3px #80bafc;
}

caption {
  margin-top: 10px;
  height: 40px;
  color: #80cefc;
}

td,
th {
  border-right: solid 3px #80bafc;
  border-bottom: solid 3px #80bafc;
  padding: 13px 20px;
  letter-spacing: 0.7px;
}

td:last-child,
th:last-child {
  border-right: none;
}

td:not(:last-child),
th:not(:last-child) {
  text-align: center;
  width: 22%;
}

td:nth-child(2),
.optional {
  font-style: italic;
}

